<template>
  <div class="detalle__contenedor">
    <div class="detalle__informacion__fondo_color">
      <img
        src="../assets/imagenes/adele.jpg"
        alt="albumCancion"
        class="detalle__informacion__fondo_img"
      />
    </div>
    <div class="detalle__informacion">
      <div>
        <img class="detalle__informacion__imagen" src="../assets/imagenes/img250.png" />
      </div>
      <div class="detalle__informacion__descripcion">
        <p class="album">{{ albumCancion + " - " + tituloCancion }}</p>
        <p class="comentario">{{ artista }}</p>
        <p class="seguidores">{{ colaboradores }}"</p>
        <div class="biografia">
          <p>{{ comentarioAlbum }}</p>
        </div>
      </div>
      <div class="detalle__informacion__botones">
        <div id="reproducir">Reproducir</div>
        <div id="seguir">Seguir</div>
        <div id="mas">
          <a :href="informacion.share"> <i class="fas fa-ellipsis-h"></i> </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from "./event-bus";
export default {
  data() {
    return {
      informacion: [],
      tituloCancion: "Busca el tema.",
      albumCancion: "Escucha.. ",
      comentarioAlbum:
        "Con los botones de aqui abajo, puedes ir al sitio de Deezer y escuchar el clip completo",
      resumen:
        "Con los botones de aqui abajo, puedes ir al sitio de Deezer y escuchar el clip completo",
      imagenartista: "../assets/imagenes/adele.jpg",
      artista: "Desarrollado por Mlros0590",
      colaboradores: "con la guìa de FractalUP",
      albumportada: "../assets/imagenes/img250.png",
      clipcompleto: "#",
      seguirartista: "#",
    };
  },
  name: "FuDetalle",
  methods: {
    llenarinfo(informacion) {
      if (informacion === undefined) {
        console.log("no se ha llenado el $Emit");
      } else {
        this.tituloCancion = informacion.title_short;
        this.albumportada = informacion.album.cover_big;
        this.albumCancion = informacion.album.title;
        this.comentarioAlbum =
          "Este clip de " +
          informacion.artist +
          " en colaboración con " +
          informacion.contributors +
          " fue lanzado el " +
          informacion.release_date;
        this.resumen =
          "Para escuchar la canción completa, ingresa a tu cuenta de Deeze y visita " +
          informacion.link;
        this.imagenartista = informacion.artist.picture_xl;
        this.colaboradores = informacion.contributors.name;
      }
    },
  },
  mounted() {
    //var actual=this;
    EventBus.$on("cargarinfo", (informacionnueva) =>
      this.llenarinfo(informacionnueva)
    );
  },
  beforeDestroy() {
    EventBus.$off("cargarinfo", (informacionnueva) =>
      this.llenarinfo(informacionnueva)
    );
  },
};
</script>

<style>
.detalle__informacion__fondo_color {
  position: absolute;
  width: 91%;
  height: 165px;
  top: 0;
  right: 4.5%;
  background: linear-gradient(0deg, rgba(167, 0, 0, 0.7), rgba(167, 0, 0, 0.7));
  opacity: 0.5;
}
.detalle__informacion__fondo_img {
  position: absolute;
  object-position: 0px 20%;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 1;
  opacity: 0.5;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  object-fit: cover;
}
#mas {
  display: inline;
  border: none;
  background: transparent;
  border-radius: 100px;
  width: 20px;
  height: 31px;
  font-size: 0.875em;
  padding: 5.5px 10px 7px 10px;
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
}
#reproducir {
  display: inline;
  border: 1px solid #e86060;
  background: #e86060;
  border-radius: 100px;
  width: 100px;
  height: 31px;
  font-size: 0.875em;
  padding: 5.5px 22px 7px 22px;
  color: #fff;
  cursor: pointer;
}
#seguir {
  display: inline;
  background: transparent;
  border-radius: 100px;
  width: 100px;
  height: 31px;
  border: 1px solid #e86060;
  font-size: 0.875em;
  box-sizing: border-box;
  padding: 5.5px 38px 7px 38px;
  cursor: pointer;
  margin-left: 10px;
  color: #e86060;
}
.detalle__informacion__botones {
  display: inline;
  position: absolute;
  width: 74%;
  top: 77%;
  right: 3%;
  background: transparent;
}
.biografia {
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 0.75em;
  line-height: 1.125em;
  line-clamp: 2;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: wrap;
}
.biografia p {
  max-height: 2.25em; /*line-height por nº de líneas*/
  overflow: hidden;
  text-align: justify;
}
.biografia p ::after {
  content: "...";
  background: #ffeeaa;
  position: absolute;
  top: 1.125em; /*max-height menos 1 line-height*/
  right: 0;
}
.album {
  font-family: Quicksand;
  font-size: 1.375em;
  font-style: normal;
  font-weight: 700;
}
.comentario {
  display: inline-block;
  font-family: Quicksand;
  font-size: 0.875em;
  font-style: normal;
  font-weight: 400;
  line-height: 0.875em;
  margin-top: -0.75px;
}
.seguidores {
  display: inline-block;
  font-family: Quicksand;
  font-style: normal;
  font-weight: normal;
  font-size: 0.75em;
  line-height: 0em;
  text-indent: 1em;
  color: #662323;
}
.detalle__informacion {
  position: absolute;
  width: 91%;
  height: 165px;
  background: transparent;
  top: 0;
  right: 4.5%;
  z-index: 3;
}
.detalle__informacion__descripcion {
  position: absolute;
  width: 74%;
  height: 165px;
  background: transparent;
  top: 0;
  right: 3%;
  line-height: 0em;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}
.detalle__informacion__imagen {
  position: absolute;
  width: 20%;
  height: 165px;
  background: transparent;
  top: 0;
  object-fit: cover;
}
/*.imagen {
  max-width: 200px;
  max-height: 200px;
}*/
.detalle__contenedor {
  position: fixed;
  top: 80px;
  right: 0px;
  width: 75%;
  height: 165px;
  background: #fff;
}

@media screen and (min-width: 701px) {
  /*VISTA PC*/
}
@media screen and (max-width: 700px) {
  /*VISTA MOVIL*/
  .detalle__contenedor {
    position: fixed;
    top: 116px;
    right: 0px;
    width: 100%;
  }
  .detalle__informacion__imagen {
    display: none;
  }
  .detalle__informacion__descripcion,
  .detalle__informacion__botones {
    width: 96%;
    right: 2%;
  }
  #reproducir,
  #seguir {
    width: 60px;
    padding: 5.5px 22px 7px 22px;
  }
  #mas {
    width: 30px;
  }
}
</style>